"use client";

import { useState } from "react";

export default function MultiStepForm() {
  const [step, setStep] = useState(1);
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    phone: "",
  });

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const nextStep = () => setStep(step + 1);
  const prevStep = () => setStep(step - 1);

  const handleSubmit = () => {
    alert(JSON.stringify(formData));
    // Тут можна відправити дані на сервер
  };

  return (
    <div className="grid grid-rows-[0px_1fr_20px] items-center justify-items-center min-h-screen sm:pt-[64px] pt-[30px] gap-16 font-[family-name:var(--font-geist-sans)]">
      <Image src="/Logo.svg" alt="Dating logo" width={48} height={88} />
      <div className="max-w-md mx-auto p-6 ">
        <button onClick={prevStep} className="">
          <span className=" transform !rotate-180 pr-2">&lt;-</span> Back
        </button>
        {step === 1 && (
          <div className="flex flex-col gap-4">
            <h2 className="text-xl font-semibold">Step 1: Your Name</h2>
            <input
              type="text"
              name="name"
              placeholder="Full Name"
              value={formData.name}
              onChange={handleChange}
              className="border p-2 rounded"
            />
            <button
              onClick={nextStep}
              className="bg-blue-500 text-white py-2 px-4 rounded">
              Next
            </button>
          </div>
        )}

        {step === 2 && (
          <div className="flex flex-col gap-4">
            <h2 className="text-xl font-semibold">Step 2: Email</h2>
            <input
              type="email"
              name="email"
              placeholder="Email"
              value={formData.email}
              onChange={handleChange}
              className="border p-2 rounded"
            />
            <div className="flex justify-between">
              <button
                onClick={prevStep}
                className="bg-gray-300 py-2 px-4 rounded">
                <span className=" transform !rotate-180">&lt;-</span> Back
              </button>
              <button
                onClick={nextStep}
                className="bg-blue-500 text-white py-2 px-4 rounded">
                Next
              </button>
            </div>
          </div>
        )}

        {step === 3 && (
          <div className="flex flex-col gap-4">
            <h2 className="text-xl font-semibold">Step 3: Phone</h2>
            <input
              type="tel"
              name="phone"
              placeholder="Phone"
              value={formData.phone}
              onChange={handleChange}
              className="border p-2 rounded"
            />
            <div className="flex justify-between">
              <button
                onClick={prevStep}
                className="bg-gray-300 py-2 px-4 rounded">
                Back
              </button>
              <button
                onClick={handleSubmit}
                className="bg-green-500 text-white py-2 px-4 rounded">
                Submit
              </button>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}
